@tailwind base;

@tailwind components;

@tailwind utilities;

@layer base {
    h1 {
        @apply text-3xl;
    }
    h2 {
        @apply text-2xl;
    }
    h3 {
        @apply text-xl
    }
    h4 {
        @apply text-xl
    }

    ul.simple {
        @apply list-disc pl-8;
    }

    ol.simple {
        @apply list-decimal pl-8;
    }

    a {
        @apply no-underline

    }

    a:hover {
        @apply underline
    }
}
